.icon {
  width: 1em;
  height: 1em;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
}
* {
  margin: 0;
  padding: 0;
}
ul,
ol {
  list-style: none;
}
.logo {
  width: 275px;
  height: 100px;
  /* border: 1px solid red; */
  margin: 100px auto 0 auto;
  background: url(data:image/svg+xml;base64,)
    no-repeat center center;
  background-size: contain;
}
.search {
  position: relative;
  max-width: 582px;
  height: 40px;
  margin: 20px auto;
  /* border: 1px solid blue; */
  border-radius: 22px;
  box-shadow: 0 1px 6px 0 rgba(32, 33, 36, 0.28);
}
.search:hover {
  box-shadow: 0 0 4px 0px #ccc;
}
.search .icon-search {
  position: absolute;
  left: 16px;
  width: 24px;
  height: 24px;
  /* border: 1px solid red; */
  top: 50%;
  transform: translateY(-50%);
  background: url(data:image/svg+xml;base64,)
    no-repeat center center;
}
.search .icon-mic {
  position: absolute;
  width: 24px;
  height: 24px;
  /* border: 1px solid blue; */
  right: 16px;
  top: calc(50% - 12px);
  background: url(data:image/svg+xml;base64,)
    no-repeat center center;
  cursor: pointer;
}
.search input {
  width: 100%;
  height: 100%;
  border-radius: 22px;
  padding: 0 40px;
  font-size: 16px;
  outline: none;
  border: none;
  /* outline: none; */
}
.buttons {
  /* border: 1px solid red; */
  text-align: center;
}
.buttons span:hover {
  cursor: pointer;
}
.buttons span {
  /* border: 1px solid blue; */
  padding: 6px 12px;
  background: #eee;
  font-size: 13px;
  color: #666;
  margin: 16px 14px;
  display: inline-block;
  border-radius: 4px;
}

.label {
  /* border: 1px solid red; */
  max-width: 582px;
  margin: 20px auto;
}
.label ul {
  display: flex;
  justify-content: space-between;
  /* border: 1px solid red; */
}
.label ul a {
  text-decoration: none;
  display: block;
  width: 100px;
  height: 100px;
  font-size: 13px;
  /* border: 1px solid blue; */
  text-align: center;
  color: rgb(var(--tile-title-color));
}
.label svg {
  /* border: 1px solid blue; */
  font-size: 45px;
  padding: 14px 0;
}
